

.congrats-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
  }

  .imagecongrats{
    width:'6%';
     height:'auto';
      margin-left:'1%';
      margin-top:'1%'
  }

  .congrats-container h2{
    color: #ffa400;
  }
  
  .icon-container {
    margin-bottom: 20px;
    margin-left: 35%;
  }
  

.gift-icon {
    font-size: 4rem;
    color: #FFD700; 
    animation: spin 2s linear infinite; 
  }
  
  @keyframes spin {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg); 
    }
  }
  
  @keyframes stop-spinning {
    0% {
      transform: rotate(0deg); 
    }
    100% {
      transform: rotate(0deg);
    }
  }
  
  
  
  .btn {
    padding: 10px 20px;
    font-size: 1rem;
    background-color: #e75553;
    color: #fff;
    border: none;
    cursor: pointer;
    border-radius: 5px;
    margin-left: 25%;
    width: 50%;
}
  
  
  .btn:hover {
    background-color: #222; 
  }
  
 
  @media only screen and (max-width: 768px) {
    .gift-icon {
      font-size: 3rem; 
    }
    
    .btn {
      padding: 8px 16px; 
      font-size: 0.9rem; 
    }
  }
  
  @media only screen and (max-width: 480px) {
    .gift-icon {
      font-size: 2.5rem;     }
    
    .btn {
      padding: 6px 12px; 
      font-size: 0.8rem; 
    }
  }

  .congrates-container1{
    background-color: #fff; 
    padding: 20px; 
    border-radius: 10px; 
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.1); 
  }

  @media only screen and (max-width: 768px) {
    .white-container {
      max-width: 90%; 
    }
  }
  