

.trophy-container {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 25%;
}

.trophy {
  color: gold;
  font-size: 2rem;
}

.trophy-text {
  margin-left: 10px;
  color: #23827a;
  font-size: 100%;
}

.trophy-text p {
  margin: 0;
}

.form-container {
  display: flex;
  justify-content: center;
  align-items: center; 
 
}

.form {
  width: 100%;
  max-width: 400px;
  padding: 20px;
}

.name,
input {
  width: 70%;
  margin-bottom: 20px;
  height: 30px;
  padding: 10px;
}

textarea {
  width: 70%;
  margin-bottom: 20px;
  height: 60px;
  padding: 10px;
}



.button-container {
  width: 100%; 
  display: flex;
  justify-content: center;
}

button[type="submit"] {
  width: 100px;
  background-color: black;
  color: white;
  border: none;
  padding: 10px;
  cursor: pointer;
}




@media only screen and (min-width: 768px) {
  .form {
    max-width: 600px;
  }
}

@media only screen and (min-width: 1024px) {
  .form {
    max-width: 800px;
  }
}

