.lemon {
    display: flex;
    flex-wrap: wrap; 
    margin-left: 20%;
}

.content h3 {
    width: 40%; 
}

.content p {
    width: 70%; 
}

.home-btn {
    padding: 10px 20px;
    font-size: 1rem;
    background-color: #e75553;
    color: #fff;
    border: none;
    cursor: pointer;
    border-radius: 5px;
    margin-left: auto; 
    margin-right: 10%; 
    width: 20%;

    
    @media only screen and (max-width: 768px) {
        margin-right: 5%;
        width: 30%; 
    }

    
    @media only screen and (max-width: 480px) {
        margin-right: 0; 
        width: 50%; 
    }
}
